




let onAppShowCallback = [],onAppHideCallback = []
// 监听应用生命周期
const appEvent = {
	appShowIndex:0,
	appHideIndex:0,
	onAppShow(callback){
		this.appShowIndex ++
		if(typeof callback == 'function'){
			onAppShowCallback.push(callback)
		}
		onAppShowCallback.forEach(fun=>fun())
	},
	onAppHide(callback){
		this.appHideIndex ++
		if(typeof callback == 'function'){
			onAppHideCallback.push(callback)
		}
		onAppHideCallback.forEach(fun=>fun())
	}
}

setTimeout(()=>{











	











	

	uni.onAppShow(appEvent.onAppShow)
	uni.onAppHide(appEvent.onAppHide)

	
},0)

export default appEvent